/*--------------------
   1. BASE CSS
----------------------*/

.alignleft {
	float: left;
	margin-right: 15px;
}

.alignright {
	float: right;
	margin-left: 15px;
}

.aligncenter {
	display: block;
	margin: 0 auto 15px;
}

a {
	color: #252525;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

a:focus,
a:hover {
	background: none;
	color: inherit;
	outline: 0;
	text-decoration: none;
}

.nav>li>a:focus,
.nav>li>a:hover {
	background: none;
}

button:focus {
	outline: 0;
}

img {
	max-width: 100%;
	height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 600;
	margin: 0 0 15px;
}

html,
body {
	height: 100%
}

body {
	font-family: 'Poppins', sans-serif;
	font-size: 16px;
	line-height: 1.5em;
	font-weight: 400;
	color: #252525;
}

ul,
ol {
	margin: 0;
	padding: 0;
	list-style: none;
}

.navbar-header .collapse,
.navbar-toggle {
	border: 2px solid #ffffff;
	border-radius: 0;
}

.navbar-toggle .icon-bar {
	background: #ffffff none repeat scroll 0 0;
}


/* Remove Chrome Input Field's Unwanted Yellow Background Color */

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
	-webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

.area-title {
	margin-bottom: 80px;
	position: relative;
}


/*-----------------------------------------*/

.area-title-and-icon {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 60px;
}

.area-icon {
	background: rgba(0, 0, 0, 0) -webkit-gradient(linear, left top, right top, from(#92fe9d), to(#00c9ff)) repeat scroll 0 0;
	background: rgba(0, 0, 0, 0) linear-gradient(to right, #92fe9d, #00c9ff) repeat scroll 0 0;
	color: transparent;
	font-size: 80px;
	height: 80px;
	line-height: 1;
	margin-right: 20px;
	width: 80px;
	-webkit-background-clip: text;
	-ms-background-clip: text;
	background-clip: text;
}

.area-title-and-icon>h2 {
	margin: 0;
}

.section-padding {
	padding: 100px 0;
}

.padding-top {
	padding-top: 100px;
}

.padding-bottom {
	padding-bottom: 100px;
}

.padding-left {
	padding-left: 50px;
}

.padding-right {
	padding-right: 50px;
}

.margin-bottom {
	margin-bottom: 50px;
}

.margin-top {
	margin-top: 50px;
}

.no-margin {
	margin: 0;
}

.no-padding {
	padding: 0;
}

.gray-bg {
	background: #f4f7f7;
}

.dark-bg {
	background: #262626;
	color: #ffffff;
}


/*--------------------------
    2. TOP AREA
---------------------------*/

.top-area {
	height: 100%;
	position: relative;
	width: 100%;
}

.top-area-bg {
	background: rgba(0, 0, 0, 0) url("../public/img/slider/slide_1.jpg") no-repeat scroll center center / cover;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.top-area-bg::after {
	background: #000000 none repeat scroll 0 0;
	content: "";
	height: 100%;
	left: 0;
	opacity: 0.75;
	position: absolute;
	top: 0;
	width: 100%;
}


/*---------------------------------
    2.1 MAINMENU AREA
-----------------------------------*/

.header-top-area {
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 999;
}

.mainmenu-area {
	position: relative;
	z-index: 9999;
}

.mainmenu-area-bg {
	background: #fff none repeat scroll 0 0;
	height: 100%;
	left: 0;
	opacity: 0.5;
	position: absolute;
	top: 0;
	-webkit-transition: all 0.3s ease-in-out 0s;
	transition: all 0.3s ease-in-out 0s;
	width: 100%;
}

.is-sticky .mainmenu-area-bg {
	-webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
	opacity: 1;
}

.navbar {
	border-bottom: 0 none;
	border-top: 0 none;
	margin-bottom: 0;
}

.logo {
	/*	display: inline-block;*/
	margin-top: 6px;
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	/*	position: relative;
	z-index: 999999;*/
}

.is-sticky .logo {
	margin-top: 3px;
}

.is-sticky .navbar-header {
	margin-top: 5px;
}

.navbar-brand {
	padding: 0;
}

.navbar-brand>img {
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	width: 100%;
}

.is-sticky .navbar-brand>img {
	width: 68%;
}

a.navbar-brand {
	color: #fff;
	font-size: 30px;
	font-weight: 600;
	letter-spacing: 3px;
	line-height: inherit;
	text-transform: uppercase;
}

.navbar-brand {
	height: auto;
}

.is-sticky .mainmenu-area {
	z-index: 99999;
}

.mainmenu {
	float: right;
}

.mainmenu li {
	display: inline;
}

.mainmenu li a {
	background: none;
	display: inline-block;
	padding: 30px 10px;
	position: relative;
	z-index: 1;
}

.mainmenu li a::before {
	background: rgba(0, 0, 0, 0) -webkit-gradient(linear, left top, right top, from(#92fe9d), to(#00c9ff)) repeat scroll 0 0;
	background: rgba(0, 0, 0, 0) linear-gradient(to right, #92fe9d, #00c9ff) repeat scroll 0 0;
	border-radius: 50px;
	content: "";
	height: 30px;
	left: 50%;
	opacity: 0;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%) translateX(-50%);
	transform: translateY(-50%) translateX(-50%);
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	width: 100%;
	z-index: -1;
}

.mainmenu li a:hover,
.mainmenu li.active a {
	background: none;
	color: #ffffff;
}

.mainmenu li a:hover::before,
.mainmenu li.active a:before {
	opacity: 1;
	width: 110%;
}

.is-sticky .mainmenu li a {
	padding: 15px 10px;
}

.mean-container .mean-bar {
	background: transparent none repeat scroll 0 0;
	min-height: 0;
	padding: 0;
}

.mean-container a.meanmenu-reveal {
	border: 1px solid #464646;
	color: #252525;
	margin-top: -37px;
	padding: 4px 5px;
	position: absolute;
}

.mean-container a.meanmenu-reveal span {
	background: #252525 none repeat scroll 0 0;
}

.mean-container .mean-nav {
	margin-top: 10px;
}


/*-----------------------------------
    2.2 TOP BAR AREA
------------------------------------*/

.top-bar {
