/*-----------------------------------
    2.2 WELCOME SLIDER AREA
-------------------------------------*/

.welcome-slider-area {
	height: 100%;
	position: relative;
	z-index: 9;
}

.welcome-slider-area.owl-carousel div:not(.owl-controls) {
	height: 100%;
}

.welcome-single-slide {
	z-index: 9;
	position: relative;
}

.welcome-single-slide:before {
	background: #000 url(../public/img/pattarn.png);
	content: "";
	height: 100%;
	left: 0;
	opacity: 0.5;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: -1;
}

.welcome-text {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	color: #ffffff;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	height: 100%;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	left: 0;
	margin-bottom: 15px;
	margin-right: 15px;
	position: relative;
	padding-top: 100px;
	top: 0;
	width: 100%;
}

.welcome-text h1 {
	-webkit-animation-delay: 0.3s;
	animation-delay: 0.3s;
	font-size: 60px;
	font-weight: 800;
	letter-spacing: 5px;
	line-height: 1;
	margin-bottom: 30px;
	position: relative;
	text-transform: uppercase;
}

.welcome-text h2 {
	-webkit-animation-delay: 0.5s;
	animation-delay: 0.5s;
	background: #ddd none repeat scroll 0 0;
	border: 1px solid #00c9ff;
	color: #252525;
	font-size: 16px;
	letter-spacing: 2px;
	margin-bottom: 30px;
	padding: 15px;
	text-transform: uppercase;
}

.welcome-text p {
	-webkit-animation-delay: 0.7s;
	animation-delay: 0.7s;
	letter-spacing: 2px;
	margin-bottom: 40px;
}

.call-to-action {
	display: inline-block;
	height: auto !important;
	margin-bottom: 0;
	margin-top: 20px;
	-webkit-animation-delay: 0.9s;
	animation-delay: 0.9s;
}

.call-to-action a {
	border: 2px solid;
	border-radius: 50px;
	color: #fff;
	display: inline-block;
	letter-spacing: 2px;
	margin: 0 5px;
	overflow: hidden;
	padding: 10px 30px;
	position: relative;
	text-transform: uppercase;
	z-index: 1;
}

.call-to-action a::before {
	background: rgba(0, 0, 0, 0) -webkit-gradient(linear, left top, right top, from(#92fe9d), to(#00c9ff)) repeat scroll 0 0;
	background: rgba(0, 0, 0, 0) linear-gradient(to right, #92fe9d, #00c9ff) repeat scroll 0 0;
	border: 2px solid;
	border-radius: 50px;
	content: "";
	height: 100%;
	left: 0;
	opacity: 1;
	position: absolute;
	top: 0;
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	width: 100%;
	z-index: -1;
}

.call-to-action a.transparent::before {
	opacity: 0;
}

.call-to-action a.transparent:hover::before {
	opacity: 1;
}

.call-to-action a:hover::before {
	opacity: 0;
}

.slider-bg-one {
	background: url(../public/img/slider/slide_1.jpg) no-repeat scroll center center / cover;
}

.slider-bg-two {
	background: url(../public/img/slider/slide_2.jpg) no-repeat scroll center center / cover;
}

.welcome-slider-area.owl-carousel .owl-nav>div {
	background: #fff none repeat scroll 0 0;
	display: inline-block;
	font-size: 30px;
	height: 40px;
	margin: 0 5px;
	padding-top: 5px;
	text-align: center;
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	width: 40px;
}

.welcome-slider-area.owl-carousel .owl-controls {
	bottom: 7%;
	position: absolute;
	right: 8.4%;
}

.welcome-slider-area.owl-carousel .owl-nav>div:hover {
	background: #00c9ff none repeat scroll 0 0;
	color: #fff;
}


