/*-----------------------------
	4. SERVICE AREA
-------------------------------*/

.service-area {
	position: relative;
}

.area-image-bg {
	background: rgba(0, 0, 0, 0) url("../public/img/area-bg.jpg") no-repeat scroll center center / cover;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.area-image-bg::after {
	background: #999 none repeat scroll 0 0;
	content: "";
	height: 100%;
	left: 0;
	opacity: 0.1;
	position: absolute;
	top: 0;
	width: 100%;
}

.service-top-content {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.service-menu {}

.service-menu li {
	display: block;
}

.service-menu li a {
	color: #252525;
	display: block;
	margin-bottom: 5px;
	padding: 10px;
	position: relative;
	z-index: 1;
}

.service-menu li.active a {
	color: #ffffff;
}

.service-menu li a::before,
.service-menu li a::after {
	background: rgba(0, 0, 0, 0) -webkit-gradient(linear, left top, right top, from(#92fe9d), to(#00c9ff)) repeat scroll 0 0;
	background: rgba(0, 0, 0, 0) linear-gradient(to right, #92fe9d, #00c9ff) repeat scroll 0 0;
	content: "";
	height: 100%;
	left: 0;
	opacity: 0;
	position: absolute;
	top: 0;
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	width: 100%;
	z-index: -1;
}

.service-menu li a::after {
	background: transparent none repeat scroll 0 0;
	border-bottom: 22px solid transparent;
	border-left: 22px solid #00c9ff;
	border-top: 22px solid transparent;
	height: 0;
	left: auto;
	opacity: 0;
	right: -22px;
	-webkit-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transform-origin: left center 0;
	transform-origin: left center 0;
	width: 0;
}

.service-menu li.active a::before,
.service-menu li:hover a::before {
	opacity: 1;
}

.service-menu li.active a::after {
	opacity: 1;
	-webkit-transform: scaleX(1);
	transform: scaleX(1);
}

.service-content {
	background: #fff none repeat scroll 0 0;
	padding: 20px;
}

.service-details {
	border: 1px solid #00c9ff;
	padding: 20px;
}


/*----------------------------
	5. TEAM AREA
------------------------------*/

.single-team {
	text-align: center;
}

.team-member-image {
	position: relative;
}

.team-member-image::before {
	left: 0;
	position: absolute;
	top: 0;
	content: "";
	width: 0;
	height: 0;
	border-top: 1px solid #00c9ff;
	border-left: 1px solid #00c9ff;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.team-member-image::after {
	bottom: 0;
	right: 0;
	position: absolute;
	content: "";
	width: 0%;
	height: 0%;
	border-right: 1px solid #00c9ff;
	border-bottom: 1px solid #00c9ff;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.single-team:hover .team-member-image:before,
.single-team:hover .team-member-image:after {
	width: 100%;
	height: 100%;
}


/*.team-member-details {
	background: #f9f9f9 none repeat scroll 0 0;
	padding: 20px;
}*/

.team-member-details {
	background: rgba(0, 0, 0, 0) url("../public/img/area-bg.jpg") no-repeat scroll center center / cover;
	padding: 20px;
}

.team-member-details h3 {
	font-weight: 500;
	margin-bottom: 5px;
}

.team-member-details .social-bookmark li a {
	color: #252525;
	height: 35px;
	padding-bottom: inherit;
	padding-left: inherit;
	padding-right: inherit;
	padding-top: 5px;
	position: relative;
	width: 35px;
	z-index: 1;
}

.team-member-details .social-bookmark li a:hover {
	color: #ffffff;
}


/*-----------------------------
	6. PROMOTIONS AREA
------------------------------*/

.promotions-area {
	position: relative;
}

.promotions-area.section-padding {
	padding-bottom: 70px;
}

.single-promotions {
	background: #fff none repeat scroll 0 0;
	margin-bottom: 30px;
	padding: 30px;
	position: relative;
	text-align: center;
	-webkit-transition: all 0.3s ease 0s;
	transition: all 0.3s ease 0s;
	z-index: 1;
}

.single-promotions::before {
	border: 1px solid #00c9ff;
	content: "";
	height: calc(100% - 20px);
	left: 10px;
	position: absolute;
	top: 10px;
	width: calc(100% - 20px);
	z-index: -1;
}

.promotions-icon {
	background: rgba(0, 0, 0, 0) -webkit-gradient(linear, left top, right top, from(#92fe9d), to(#00c9ff)) repeat scroll 0 0;
	background: rgba(0, 0, 0, 0) linear-gradient(to right, #92fe9d, #00c9ff) repeat scroll 0 0;
	color: transparent;
	font-size: 36px;
	margin-bottom: 20px;
	-webkit-background-clip: text;
	-ms-background-clip: text;
	background-clip: text;
}

.single-promotions h4 {
	font-weight: 500;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.single-promotions:hover {
	-webkit-box-shadow: 0 10px 15px #999;
	box-shadow: 0 10px 15px #999;
	-webkit-transform: translateY(-5px);
	transform: translateY(-5px);
}

.single-promotions:hover h4 {
	color: #00c9ff;
}


/*-----------------------------
	7. TESTMONIAL AREA
-------------------------------*/

.testmonial-area {
	position: relative;
}

.testmonial-area-bg {
	background: url("../public/img/testmonial/testmonial-right-bg.jpg") no-repeat scroll right center / 50% 100%, rgba(0, 0, 0, 0) -webkit-gradient(linear, right top, left bottom, from(#00c9ff), to(#92fe9d)) repeat scroll 0 0;
	background: url("../public/img/testmonial/testmonial-right-bg.jpg") no-repeat scroll right center / 50% 100%, rgba(0, 0, 0, 0) linear-gradient(to left bottom, #00c9ff, #92fe9d) repeat scroll 0 0;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.single-testmonial {
	padding-top: 50px;
	position: relative;
}

.member-thumb {
	border-radius: 50%;
	height: 100px;
	left: 50%;
	overflow: hidden;
	position: absolute;
	top: 0;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
	width: 100px;
	z-index: 999;
}

.member-details {
	color: #ffffff;
	border-radius: 50%;
	padding: 70px 40px;
	z-index: -1;
}

.member-details h3 {
	color: #ffffff;
	display: inline-block;
	font-size: 18px;
	font-weight: 500;
	letter-spacing: 2px;
	padding-bottom: 15px;
	position: relative;
	text-transform: uppercase;
}

.member-details h3::after {
	background: #fff none repeat scroll 0 0;
	bottom: 0;
	content: "";
	height: 2px;
	left: 50%;
	position: absolute;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
	width: 100px;
}

.testmonial-area .owl-controls {
	bottom: 20px;
	left: 50%;
	position: absolute;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}

.testmonial-area .owl-dot {
	background: rgba(255, 255, 255, .5);
	border-radius: 50%;
	display: inline-block;
	height: 15px;
	margin: 0 5px;
	width: 15px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.testmonial-area .owl-dot.active {
	background: rgba(255, 255, 255, 1);
}


