	top: 10px;
	width: calc(100% - 20px);
	z-index: -1;
}

.promotions-icon {
	background: rgba(0, 0, 0, 0) -webkit-gradient(linear, left top, right top, from(#92fe9d), to(#00c9ff)) repeat scroll 0 0;
	background: rgba(0, 0, 0, 0) linear-gradient(to right, #92fe9d, #00c9ff) repeat scroll 0 0;
	color: transparent;
	font-size: 36px;
	margin-bottom: 20px;
	-webkit-background-clip: text;
	-ms-background-clip: text;
	background-clip: text;
}

.single-promotions h4 {
	font-weight: 500;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.single-promotions:hover {
	-webkit-box-shadow: 0 10px 15px #999;
	box-shadow: 0 10px 15px #999;
	-webkit-transform: translateY(-5px);
	transform: translateY(-5px);
}

.single-promotions:hover h4 {
	color: #00c9ff;
}


/*-----------------------------
	7. TESTMONIAL AREA
-------------------------------*/

.testmonial-area {
	position: relative;
}

.testmonial-area-bg {
	background: url("../public/img/testmonial/testmonial-right-bg.jpg") no-repeat scroll right center / 50% 100%, rgba(0, 0, 0, 0) -webkit-gradient(linear, right top, left bottom, from(#00c9ff), to(#92fe9d)) repeat scroll 0 0;
	background: url("../public/img/testmonial/testmonial-right-bg.jpg") no-repeat scroll right center / 50% 100%, rgba(0, 0, 0, 0) linear-gradient(to left bottom, #00c9ff, #92fe9d) repeat scroll 0 0;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.single-testmonial {
	padding-top: 50px;
	position: relative;
}

.member-thumb {
	border-radius: 50%;
	height: 100px;
	left: 50%;
	overflow: hidden;
	position: absolute;
	top: 0;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
	width: 100px;
	z-index: 999;
}

.member-details {
	color: #ffffff;
	border-radius: 50%;
	padding: 70px 40px;
	z-index: -1;
}

.member-details h3 {
	color: #ffffff;
	display: inline-block;
	font-size: 18px;
	font-weight: 500;
	letter-spacing: 2px;
	padding-bottom: 15px;
	position: relative;
	text-transform: uppercase;
}

.member-details h3::after {
	background: #fff none repeat scroll 0 0;
	bottom: 0;
	content: "";
	height: 2px;
	left: 50%;
	position: absolute;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
	width: 100px;
}

.testmonial-area .owl-controls {
	bottom: 20px;
	left: 50%;
	position: absolute;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}

.testmonial-area .owl-dot {
	background: rgba(255, 255, 255, .5);
	border-radius: 50%;
	display: inline-block;
	height: 15px;
	margin: 0 5px;
	width: 15px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

.testmonial-area .owl-dot.active {
	background: rgba(255, 255, 255, 1);
}


